$main-color: #383837;
$secound-color: #b3b4ad;
// COLORS
$color-primary: #55c57a;
$color-primary-light: #7ed56f;
$color-primary-dark: #28b485;

$color-secondary-light: #ffb900;
$color-secondary-dark: #ff7730;

$color-primary-red: rgb(245, 38, 38);
$color-secoundary-red: rgb(116, 4, 4);

$color-tertiary-light: #2998ff;
$color-tertiary-dark: #5643fa;

$color-grey-light-1: #f7f7f7;
$color-grey-light-2: #eee;

$color-grey-dark: #777;
$color-grey-dark-2: #999;
$color-grey-dark-3: #333;

$color-white: #fff;
$color-black: #000;

// FONT
$default-font-size: 1.6rem;

// GRID
$grid-width: 114rem;
$gutter-vertical: 8rem;
$gutter-vertical-small: 6rem;
$gutter-horizontal: 6rem;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  box-sizing: border-box;
  background: linear-gradient(to bottom left, rgb(12, 100, 66), black) no-repeat
    center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  display: flex;
  text-align: center;
  flex-direction: column;
}

.container {
  background-image: linear-gradient(
      to right bottom,
      rgba($color-primary-light, 0.8),
      rgba($color-primary-dark, 0.8)
    ),
    url('../../img/background.jpg');
  display: flex;
  flex-direction: column;
  text-align: left;

  // margin: 5%;
  // padding: 5%;
  height: 100vh;

  .search {
    padding: 50px;

    .search__poke {
      height: 20vh;
    }
  }

  .pokedex {
    background-color: #bada55;
    position: absolute;
    top: 0;
    right: 0;
    padding: 50px;
    height: 85vh;
    max-height: 85vh;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-left: solid black 5px;

    .pokemon_element {
      cursor: pointer;
      list-style: none;
      text-transform: capitalize;
      font-size: 20px;
    }
    .pokemon_element:hover {
      .img_pokemon {
        display: block;
        height: fit-content;
        animation: scale 1s ease-in-out;
      }

      .details {
        display: block;
        border: 5px solid red;
        border-radius: 10px;
      }
    }

    .img_pokemon {
      display: none;
    }
    .details {
      display: none;
    }
  }

  .pokedex:hover {
    // transform: scaleX(1.3);
  }
}

.pokedex_container:hover {
}

.abilitiDescription {
  // font-size: 15px;
  display: block;
}
.hover {
  display: none;
}

@keyframes scale {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(4);
  }
}

.btn {
  &,
  &:link,
  &:visited {
    max-width: 300px;
    text-transform: uppercase;
    text-decoration: none;
    padding: 1rem 2rem;
    display: inline-block;
    border-radius: 10rem;
    transition: all 0.2s;
    position: relative;
    font-size: $default-font-size;

    //Change for the <button> element
    border: none;
    cursor: pointer;
  }

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 1rem 2rem rgba($color-black, 0.2);

    &::after {
      transform: scaleX(1.4) scaleY(1.6);
      opacity: 0;
    }
  }

  &:active,
  &:focus {
    outline: none;
    transform: translateY(-1px);
    box-shadow: 0 0.5rem 1rem rgba($color-black, 0.2);
  }

  &--red {
    background-color: $color-primary-red;
    color: $color-grey-dark;

    &::after {
      background-color: $color-secoundary-red;
    }
  }

  &--white {
    background-color: $color-white;
    color: $color-grey-dark;

    &::after {
      background-color: $color-white;
    }
  }

  &--green {
    background-color: $color-primary;
    color: $color-white;

    &::after {
      background-color: $color-primary;
    }
  }

  &::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 10rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all 0.4s;
  }

  &--animated {
    animation: moveInBottom 0.5s ease-out 0.75s;
    animation-fill-mode: backwards;
  }
}

.btn-text {
  &:link,
  &:visited {
    font-size: $default-font-size;
    color: $color-primary;
    display: inline-block;
    text-decoration: none;
    border-bottom: 1px solid $color-primary;
    padding: 3px;
    transition: all 0.2s;
  }

  &:hover {
    background-color: $color-primary;
    color: $color-white;
    box-shadow: 0 1rem 2rem rgba($color-black, 0.15);
    transform: translateY(-2px);
  }

  &:active {
    box-shadow: 0 0.5rem 1rem rgba($color-black, 0.15);
    transform: translateY(0);
  }
}

.input {
  font-size: 1.5rem;
  font-family: inherit;
  color: inherit;
  padding: 1.5rem 2rem;
  border-radius: 2rem;
  background-color: rgba($color-tertiary-dark, 0.5);
  border: none;
  border-bottom: 3px solid transparent;
  display: block;
  transition: all 0.3s;

  &:focus {
    outline: none;
    box-shadow: 0 1rem 2rem rgba($color-black, 0.1);
    border-bottom: 3px solid $color-primary;
  }

  &:focus:invalid {
    border-bottom: 3px solid $color-secondary-dark;
  }

  &::-webkit-input-placeholder {
    color: $color-grey-dark-2;
  }
}

.pokedex_pokemon {
  display: flex;
  justify-content: center;
  transform: scale(2);
}

.show_pokemon {
  display: flex;
  justify-content: center;
  flex-direction: column-reverse;
}
